<template>
  <v-flex v-if="this.$vnode.key != 'length'" xs12 pa-2>
      <v-card py-2 class="ContentItem">
        <v-card-title primary-title>
          <h1>
            <vue-markdown>{{ content.title }}</vue-markdown>
          </h1>
        </v-card-title>
        <v-card-text>
          <vue-markdown>{{content.text | truncate(100)}}</vue-markdown>
        </v-card-text>

        <v-card-actions>

          <v-btn
            @click="moreDetail()"
          >
            More
          </v-btn>

          <v-dialog
              v-model="postEdit"
              v-if="!signedOut"
              fullscreen
              transition="dialog-bottom-transition"
          >
            <v-btn 
                slot="activator"
            >Edit Post</v-btn>
            <v-card>
              <v-toolbar dark color="grey darken-4">
                <v-toolbar-title text-xs-center>
                  <h1>Edit  Post</h1>
                </v-toolbar-title>
                <v-spacer/>
                <v-btn
                  icon
                  @click="postEdit = false"
                ><v-icon>close</v-icon></v-btn>
              </v-toolbar>

              <v-form v-model="validPost" @submit="editPost">
                  <v-flex xs12 pa-2>
                      <v-text-field
                          v-model="newTitle"
                          :rules="editPostRules"
                          flat
                          dark
                          required
                          label="New Post Title"
                      ></v-text-field>
                  </v-flex>
                  <v-flex xs12 pa-2>
                      <v-textarea
                          v-model="newText"
                          :rules="editPostRules"
                          flat
                          dark
                          required
                          label="New Post Text"
                      ></v-textarea>
                  </v-flex>
                  <v-divider/>
                  <v-flex xs12 text-xs-left pa-2>
                    <h1>
                      <vue-markdown :source="newTitle" />
                    </h1>
                  </v-flex>

                  <v-flex xs12 text-xs-left pa-2>
                    <vue-markdown :source="newText" />
                  </v-flex>
                  <v-divider/>
                  <v-layout pa-2 text-xs-center>
                    <v-flex>
                            <v-btn 
                            dark
                            @click="postEdit = false"
                          >Cancel</v-btn>
                    </v-flex>
                    <v-flex>
                          <v-btn
                              type="submit"
                              :disabled="!validPost"
                              @click="postEdit = false"
                              dark
                              pa-2
                          >Confirm Edit</v-btn>
                    </v-flex>
                  </v-layout>
              </v-form>
            </v-card>
          </v-dialog>

          <v-dialog
              v-model="confirmDel"
              v-if="!signedOut"
              persistent
              :width=dialogSize
              transition="dialog-bottom-transition"
          >
            <v-btn 
                slot="activator"
            >Delete Post</v-btn>
            <v-card>
              <v-toolbar dark color="grey darken-4">
                <v-toolbar-title text-xs-center>
                  <h3>Delete Confirmation</h3>
                </v-toolbar-title>
                <v-spacer/>
                <v-btn
                  icon
                  @click="confirmDel = false"
                ><v-icon>close</v-icon></v-btn>
              </v-toolbar>
              <v-flex text-xs-center>
                <h1>Are you sure you want to delete this post?</h1>
              </v-flex>
              <v-layout pa-2>                 
                <v-flex text-xs-center>
                  <v-btn
                    v-if="!signedOut"
                    @click="confirmDel = false"
                  >No
                  </v-btn>
                </v-flex>
                <v-flex text-xs-center>
                  <v-btn
                    v-if="!signedOut"
                    @click="deletePost"
                  >Yes
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-card>
          </v-dialog> 
        </v-card-actions>
      </v-card>
  </v-flex>
</template>

<script>
import VueMarkdown from 'vue-markdown'
import firebase from 'firebase'

export default {
  name: 'ContentItem',
  components: {
    VueMarkdown
  },
  props: {
    content: {
      type: Object,
      required: true
    },
    postType: {
      type: String
    },
    key: {
      type: Object
    }
  },
  data () {
    return {
      signedOut: "",
      editPostRules:[
        v => !!v || "New statement can't be "+ v
      ],
      newText: "",
      newTitle: "",
      postId: "",
      postEdit: false,
      confirmDel: false,
      dialogSize: window.innerWidth / 4
    }
  },
  created () {
    this.signedOut = !firebase.auth().currentUser;
    this.newText = this.content.text;
    this.newTitle = this.content.title;
    this.postId = this.$vnode.key;
  },
  methods: {
    editPost () {
      const editPostRef = firebase.database().ref().child(this.postType).child(this.$vnode.key);
      var editedPostData = {
        title: this.newTitle,
        text: this.newText
      }
      editPostRef.set(editedPostData, function (error){
        if (error){
          alert("Uh Oh! " + error)
        } else {
          alert("Post Edited Successfully!")
        }
      })
    },
    deletePost () {
      const postRef = firebase.database().ref().child(this.postType).child(this.$vnode.key);
      postRef.remove()
    },
    moreDetail () {
      var postUrl = new String (this.$route.path)
      if (postUrl.includes('blog')){
        this.$router.push('/blog/'+this.postId)
      }
    }
  }
}
</script>