<template>
	<v-container fluid>
		<v-layout row wrap>
			<ContentItem
				v-for="(value, key) in contents"
				:content="value"
				:postType="postType"
				:key="key"
			/>

			<v-flex xs12 text-xs-center>
				<v-dialog
					v-model="postAdd"
					v-if="!signedOut"
					fullscreen
					transition="dialog-bottom-transition"
				>
					<v-btn
						slot="activator"
					>Add Post</v-btn>

					<v-card>
						<v-toolbar dark color="grey darken-4">
							<v-toolbar-title text-xs-center>
								<h1>
								New Post
								</h1>
							</v-toolbar-title>
							<v-spacer/>
							<v-btn
								icon
								@click="postAdd = false"
							><v-icon>close</v-icon></v-btn>
						</v-toolbar>
						
						<v-flex xs12 text-xs-center>
							<v-form v-model="validPost" @submit="addPost">
								<v-flex xs12 pa-2>
									<v-text-field
										v-model="newTitle"
										:rules="titleRules"
										dark
										required
										label="New Post Title"
									></v-text-field>
								</v-flex>
								<v-flex xs12 pa-2>
									<v-textarea
										v-model="newText"
										:rules="textRules"
										dark
										required
										label="New Post Text"
									></v-textarea>
								</v-flex>
								<v-divider/>
								<v-flex xs12 text-xs-left pa-2>
									<h1>
										<vue-markdown :source="newTitle" />
									</h1>
								</v-flex>
								<v-flex xs12 text-xs-left pa-2>
									<vue-markdown :source="newText" />
								</v-flex>
								<v-divider diplay-4/>
								<v-layout pa-2>
									<v-flex>
										<v-btn 
											dark
											@click="postAdd = false"
										>Cancel</v-btn>
									</v-flex>
									<v-flex>
										<v-btn
											type="submit"
											:disabled="!validPost"
											@click="postAdd = false"
											dark
											pa-2
										>Create New Post</v-btn>
									</v-flex>
								</v-layout>
							</v-form>
						</v-flex>
					</v-card>

				</v-dialog>
			</v-flex>
		</v-layout>
	</v-container>
</template>

<script>
import firebase from 'firebase'
import VueMarkdown from 'vue-markdown'
import ContentItem from "./ContentItem.vue"

export default {
	name: 'ContentContainer',
	components: {
		ContentItem,
        VueMarkdown
    
	},
	props: {
		contents: {
			type: Array
		},
		postType: {
			type: String
		}
	},
	data () {
		return {
			textRules:[
				(v) => !!v || "New statement can't be "+ v
			],titleRules:[
				(v) => !!v || "New statement can't be "+ v
			],
			newText: "",
			newTitle: "",
			postAdd: false
		}
	},
	created () {
		this.signedOut = !firebase.auth().currentUser;	
	},
	methods: {
		addPost () {
			const postRef = firebase.database().ref().child(this.postType);
			var newPostData = {
				title: this.newTitle,
				text: this.newText
			}
			postRef.push(newPostData, function(error) {
				if (error){
					alert("Uh Oh! "+ error)
				} else {
					alert("Post Added Successfully")
				}
			})
		}
	}
}
</script>