<template>
    <v-layout wrap>
        <v-flex xs12 pa-3 text-xs-center>
            <h1>Welcome <br/> {{Uname}}</h1>
        </v-flex>

        <v-flex xs6 pa-3 text-xs-center>
            <v-dialog
            v-model="settingMenu"
            fullscreen
            transition="dialog-bottom-transition"
            >
                <v-btn 
                    slot="activator"
                >Settings</v-btn>

                <v-card>
                    <v-toolbar dark color="grey darken-4">
                        <v-toolbar-title text-xs-center>
                            <h1>
                            Settings
                            </h1>
                        </v-toolbar-title>
                        <v-spacer/>
                        <v-btn
                            icon
                            @click="settingMenu = false"
                        ><v-icon>close</v-icon></v-btn>
                    </v-toolbar>

                    <v-layout>
                        <v-flex xs6>
                            <v-form v-model="validProfession" @submit="changeProfession">
                                
                                <v-flex pa-2 pt-4 xs12 text-xs-center class="title">
                                    Current Profession: {{currentProfession}}
                                </v-flex>

                                <v-flex xs12 pa-2>
                                    <v-text-field
                                        v-model="newProfession"
                                        :rules="newProfessionlRules"
                                        dark
                                        label="New Profession"
                                    ></v-text-field>
                                </v-flex>

                                <v-flex xs12 pa-2 text-xs-center>
                                    <v-btn
                                        type="submit"
                                        :disabled="!validProfession"
                                        dark
                                        pa-2
                                    >Submit</v-btn>
                                </v-flex>

                            </v-form>
                        </v-flex>

                        <v-flex xs6>
                            <v-form v-model="validProfession" @submit="changeProfession">
                                
                                <v-flex pa-2 pt-4 xs12 text-xs-center class="title">
                                    Current Profession: {{currentProfession}}
                                </v-flex>

                                <v-flex xs12 pa-2>
                                    <v-text-field
                                        v-model="newProfession"
                                        :rules="newProfessionlRules"
                                        dark
                                        label="New Profession"
                                    ></v-text-field>
                                </v-flex>

                                <v-flex xs12 pa-2 text-xs-center>
                                    <v-btn
                                        type="submit"
                                        :disabled="!validProfession"
                                        dark
                                        pa-2
                                    >Submit</v-btn>
                                </v-flex>

                            </v-form>
                        </v-flex>
                    </v-layout>
                    
                </v-card>
            </v-dialog>
        </v-flex>

        <v-flex xs12 pa-3 text-xs-center>
            <v-btn @click="logout">
                Logout
            </v-btn>
        </v-flex>

    </v-layout>
</template>

<script>
import firebase from 'firebase'
import App from '../App.vue'

export default {
    name: "UserPage",
    data () {
        return {
            Uname: firebase.auth().currentUser.email,
            currentProfession: "",
            newProfession: "",
            newProfessionlRules: [
                v => !!v || "New profession can't be blank!"
            ],
            currentPosts: [],
            settingMenu: false
        }
    },
    methods: {
        logout: function() {
            firebase.auth().signOut().then(function() {
                App.router.replace('/')
            }, function(err) {
                alert("Uh Oh! " + err.message)
            });
        },
        changeProfession: function() {
            const professionRef = firebase.database().ref('status').child('profession')
            professionRef.set(this.newProfession)
        },
        toggleMaintenance: function() {
            this.maintenanceMode = !this.maintenanceMode
            const maintenanceRef = firebase.database().ref('status').child('maintenance')
            maintenanceRef.set(this.maintenanceMode)
        }
    },
    created() {
        const professionRef = firebase.database().ref('status').child('profession')
        professionRef.on('value', snapshot => {
            this.currentProfession = snapshot.val();
        })
        const postsRef = firebase.database().ref('posts')
        postsRef.on('value', snapshot => {
            this.currentPosts = snapshot.val();
        })        
    }
}
</script>