<template>
    <v-container fluid>
        <v-flex>
            <v-card py-2>
                <v-card-title primary-title>
                <h1>
                    <vue-markdown :source="content.title"/>
                </h1>
                </v-card-title>
                <v-card-text>
                    <vue-markdown :source="content.text"/>
                </v-card-text>
                <v-card-actions>
                    <v-btn @click="$router.go(-1)">
                        Back
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-flex>
    </v-container>
</template>

<script>
import VueMarkdown from 'vue-markdown'
import firebase from 'firebase'

export default {
    name: "ContentDetails",
    components: {
        VueMarkdown
    },
    data () {
        return {
            content: null
        }
    },
    created () {
        var postId = this.$route.params.id
        var postUrl = new String (this.$route.path)

        if(postUrl.includes("blog")){
            const postRef = firebase.database().ref('posts').child(postId)
            postRef.on('value', snapshot => {
                if (snapshot.val() != null){
                    this.content = snapshot.val()
                } else {
                    // Redirect to /blog
                    alert("Whoops, There's Nothing Here")
                    this.$router.replace('/blog/')
                }
            })
        }
    },
    beforeCreate () {
    }
}
</script>
